import React, { Component, Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { GlobalState } from '../../reducers/index.reducer';
import {
  DEAUTHENTICATE,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST,
  ADD_FLASH_MESSAGE
} from '../../actions/index.actions';
import {
  API_ROOT,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';
import Logo from './tonalify.svg';

interface NavbarProps {
  deauthenticate: Function;
  addFlashMessage: Function;
  addPendingRequest: Function;
  finishPendingRequest: Function;
  isAuthenticated: boolean;
}

interface NavbarState {
  mobile: boolean;
}

class Navbar extends Component<NavbarProps, NavbarState> {
  constructor(props: NavbarProps) {
    super(props);
    this.state = {
      mobile: false
    };
  }
  async logout() {
    const requestId = uuidv4();
    this.props.addPendingRequest({
      id: requestId,
      type: AJAX_REQUEST_TYPES.PROFILE,
      message: 'Logging Out'
    });
    const loggedOut = await axios.get(`${API_ROOT}/logout`, {
      withCredentials: true
    });

    this.props.finishPendingRequest(requestId);

    if (!loggedOut || !loggedOut.data) return;
    localStorage.removeItem('logged_in_tonalify');
    this.props.deauthenticate();
  }

  async login() {
    const requestId = uuidv4();

    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.PROFILE,
        message: 'Logging In'
      });
      const loginUri: { data: string } = await axios.get(`${API_ROOT}/login`);
      this.props.finishPendingRequest(requestId);
      if (loginUri && loginUri.data) {
        window.location.href = loginUri.data;
        return;
      }
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Login URL',
        id: uuidv4()
      });
    } catch (error) {
      this.props.finishPendingRequest(requestId);
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Login URL',
        id: uuidv4()
      });
    }
  }

  clickHamburger() {
    this.setState({ mobile: !!this.state.mobile ? false : true });
  }
  render() {
    const { isAuthenticated } = this.props;
    return (
      <nav className="navbar-wrapper navbar is-spaced">
        <div className="navbar-brand">
          <NavLink
            className="navbar-tonalify-link navbar-item is-family-sans-serif has-text-primary has-text-weight-bold"
            to="/home"
          >
            <img src={Logo} />
          </NavLink>

          <a
            role="button"
            className={`navbar-burger burger ${
              this.state.mobile ? 'is-active' : ''
            }`}
            aria-label="menu"
            data-target="nav-menu"
            onClick={e => this.clickHamburger()}
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div
          id="nav-menu"
          className={`navbar-menu ${this.state.mobile ? 'is-active' : ''}`}
        >
          <div className="navbar-start">
            <NavLink
              className="navbar-item"
              to="/home"
              activeClassName="is-active"
            >
              Home
            </NavLink>
            {isAuthenticated ? (
              <Fragment>
                <NavLink
                  className="navbar-item"
                  to="/search"
                  activeClassName="is-active"
                >
                  Search
                </NavLink>
                <NavLink
                  className="navbar-item"
                  to="/your-playlists"
                  activeClassName="is-active"
                >
                  Playlists
                </NavLink>
                <NavLink
                  className="navbar-item"
                  to="/your-albums"
                  activeClassName="is-active"
                >
                  Albums
                </NavLink>
              </Fragment>
            ) : null}
            <NavLink
              className="navbar-item"
              to="/selection"
              activeClassName="is-active"
            >
              Export
            </NavLink>
          </div>
          <div className="navbar-end">
            <div className="navbar-item">
              <div className="buttons">
                {this.props.isAuthenticated ? (
                  <a className="button is-light" onClick={() => this.logout()}>
                    Logout
                  </a>
                ) : (
                  <a className="button is-light" onClick={() => this.login()}>
                    Login
                  </a>
                )}
              </div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

function mapStateToProps(state: GlobalState) {
  const { isAuthenticated } = state.auth;
  return { isAuthenticated };
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    deauthenticate: () => dispatch({ type: DEAUTHENTICATE }),
    addFlashMessage: (message: any) =>
      dispatch({ type: ADD_FLASH_MESSAGE, payload: message }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id })
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
