import React, { Component } from 'react';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { GlobalState } from '../../reducers/index.reducer';
import {
  AUTHENTICATE,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST,
  ADD_FLASH_MESSAGE
} from '../../actions/index.actions';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';

import Search from '../search/Search';
import { get_local_storage_status } from '../../checkStorage';

axios.defaults.withCredentials = true;

interface HomeProps {
  authenticate: Function;
  isAuthenticated: boolean;
  addPendingRequest: Function;
  finishPendingRequest: Function;
  addFlashMessage: Function;
}

interface HomeState {
  displayName: string;
}

class Home extends Component<HomeProps, HomeState> {
  constructor(props: HomeProps) {
    super(props);
    this.state = { displayName: '' };
    get_local_storage_status();
  }

  componentDidMount() {
    const code = new URLSearchParams(window.location.search).get('code');
    if (code) {
      this.authenticateUser();
      return;
    }
    if (
      this.props.isAuthenticated ||
      !localStorage.getItem('logged_in_tonalify')
    )
      return;

    this.getProfile();
  }

  async authenticateUser() {
    const requestId = uuidv4();
    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.PROFILE,
        message: 'Authenticating'
      });
      await axios.get(
        `${API_ROOT}/authorize?code=${new URLSearchParams(
          window.location.search
        ).get('code')}`,
        {
          withCredentials: true
        }
      );

      this.getProfile();

      this.props.finishPendingRequest(requestId);
    } catch (error) {
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Authenticating',
        id: uuidv4()
      });
      this.props.finishPendingRequest(requestId);
    }
  }

  async getProfile() {
    const requestId = uuidv4();
    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.PROFILE,
        message: 'Fetching Profile'
      });
      const profile = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: 'https://api.spotify.com/v1/me',
          spotify_payload: null,
          crud_operation: 'get'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      this.props.finishPendingRequest(requestId);

      if (profile.data && profile.data.id) {
        this.props.authenticate(profile.data.id);
        this.props.addFlashMessage({
          type: FLASH_MESSAGE_TYPES.SUCCESS,
          text: 'Logged In!',
          id: uuidv4()
        });
        this.setState({ displayName: profile.data.display_name });
        localStorage.setItem('logged_in_tonalify', profile.data.display_name);

        return;
      }
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Profile',
        id: uuidv4()
      });
    } catch (error) {
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Profile',
        id: uuidv4()
      });
      this.props.finishPendingRequest(requestId);
    }
  }

  async login() {
    const requestId = uuidv4();

    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.PROFILE,
        message: 'Logging In'
      });
      const loginUri: { data: string } = await axios.get(`${API_ROOT}/login`);
      this.props.finishPendingRequest(requestId);
      if (loginUri && loginUri.data) {
        window.location.href = loginUri.data;
        return;
      }
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Login URL',
        id: uuidv4()
      });
    } catch (error) {
      this.props.finishPendingRequest(requestId);
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Fetching Login URL',
        id: uuidv4()
      });
    }
  }

  render() {
    const { isAuthenticated } = this.props;
    const displayName = localStorage.getItem('logged_in_tonalify');
    return (
      <div className="home-view-wrapper">
        {isAuthenticated ? (
          <div className="section">
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <div
                className="has-text-centered"
                style={{
                  maxWidth: '400px',
                  display: 'flex',
                  alignItems: 'center',
                  flexDirection: 'column'
                }}
              >
                <p className="title">Welcome, {displayName}</p>{' '}
                <p>
                  Browse your library and make track selections based on key or
                  tempo, then export your selection as a Spotify playlist. This
                  app won't modify your playlists, it will only add new
                  playlists that you create
                </p>
              </div>
            </div>
            <div className="section">
              <div className="container is-widescreen has-text-centered">
                <div className="links">
                  <NavLink
                    className="link-card has-background-primary has-text-white is-size-4"
                    to="/search"
                  >
                    <p>Search</p>
                  </NavLink>
                  <NavLink
                    className="link-card has-background-primary has-text-white is-size-4"
                    to="/your-albums"
                  >
                    <p>Your Albums</p>
                  </NavLink>

                  <NavLink
                    className="link-card has-background-primary has-text-white is-size-4"
                    to="/your-playlists"
                  >
                    <p>Your Playlists</p>
                  </NavLink>

                  <NavLink
                    className="link-card has-background-primary has-text-white is-size-4"
                    to="/selection"
                  >
                    <p>Export Selection</p>
                  </NavLink>
                </div>
                <hr />
                <div className="buy-me-coffee-wrapper">
                  <a
                    className="bmc-button has-background-link"
                    href="https://www.buymeacoffee.com/tonalify"
                  >
                    <img
                      src="https://cdn.buymeacoffee.com/buttons/bmc-new-btn-logo.svg"
                      alt="Buy me a coffee"
                    />
                    <span
                      style={{ marginLeft: '5px', fontSize: '19px !important' }}
                    >
                      Buy me a coffee
                    </span>
                  </a>
                </div>
              </div>
            </div>
          </div>
        ) : null}
        {!isAuthenticated ? <Search login={() => this.login()} /> : null}
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    authenticate: (userId: string) =>
      dispatch({ type: AUTHENTICATE, payload: userId }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id }),
    addFlashMessage: (message: any) =>
      dispatch({ type: ADD_FLASH_MESSAGE, payload: message })
  };
};

function mapStateToProps(state: GlobalState) {
  const { isAuthenticated } = state.auth;
  return { isAuthenticated };
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
