import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { withRouter } from 'react-router';

import SearchIcon from '@material-ui/icons/Search';
import { compose } from 'redux';

import {
  ADD_FLASH_MESSAGE,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST
} from '../../actions/index.actions';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES
} from '../../constants/constants';
import TracksTable from '../tracks/TracksTable';
import { get_local_storage_status } from '../../checkStorage';

enum SearchBy {
  ARTIST = 'ARTIST',
  TRACK = 'TRACK'
}

interface SearchProps {
  route: any;
  history: any;
  addPendingRequest: Function;
  finishPendingRequest: Function;
  addFlashMessage: Function;
  login?: Function;
  isAuthenticated: boolean;
}

interface SearchState {
  filtered: boolean;
  sort: any;
  errorFetchingItems: boolean;
  disableTable: boolean;
  query: string;
  next: boolean;
  itemList: any;
  searchBy: SearchBy;
  artists: any;
  artistsPath: any;
  nextArtists: any;
  albumsPath: any;
  nextAlbums: any;
  albums: any;
  selectedAlbum: any;
  showAlbumTracksTable: boolean;
  showTracksTable: boolean;
  showArtistAlbums: boolean;
}

class Search extends Component<SearchProps, SearchState> {
  constructor(props: SearchProps) {
    super(props);

    this.state = {
      itemList: [],
      filtered: false,
      sort: { column: null, direction: null },
      errorFetchingItems: false,
      disableTable: false,
      query: '',
      next: false,
      searchBy: SearchBy.TRACK,
      artists: [],
      artistsPath: '',
      nextArtists: '',
      albums: [],
      albumsPath: '',
      nextAlbums: '',
      selectedAlbum: null,
      showAlbumTracksTable: false,
      showTracksTable: false,
      showArtistAlbums: true
    };

    get_local_storage_status();
  }

  search(e: any) {
    e.preventDefault();

    const { searchBy, query } = this.state;
    if (searchBy === SearchBy.TRACK) {
      localStorage.setItem(
        'spotify_path',
        `https://api.spotify.com/v1/search?q=${query}&type=${searchBy.toLowerCase()}&market=US`
      );

      localStorage.setItem('spotify_item_title', query);

      this.setState(
        { showTracksTable: false, showAlbumTracksTable: false },
        () => {
          this.setState({ showTracksTable: true });
        }
      );
    }

    if (searchBy === SearchBy.ARTIST) {
      this.setState({ artists: [], nextArtists: '', albums: [] }, () => {
        this.getArtists();
      });
    }

    this.setState({ filtered: true });
  }

  async getArtists() {
    const { query, nextArtists } = this.state;
    const requestId = uuidv4();
    this.props.addPendingRequest({
      id: requestId,
      type: AJAX_REQUEST_TYPES.USER_LIBRARY,
      message: 'Fetching Artists'
    });
    try {
      const artists = await axios.post(
        `${API_ROOT}/spotify-search`,
        {
          spotify_path:
            nextArtists && nextArtists.length
              ? nextArtists
              : `https://api.spotify.com/v1/search?q=${query}&type=artist&market=US`,
          is_tracks: false
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      if (!artists || !artists.data || !artists.data.artists) return;

      this.setState({
        artists: [...this.state.artists, ...artists.data.artists.items],
        nextArtists: artists.data.artists.next,
        showArtistAlbums: true
      });
      this.props.finishPendingRequest(requestId);
    } catch {
      this.props.finishPendingRequest(requestId);
    }
  }

  async getAlbums(id: string) {
    const { nextAlbums } = this.state;
    const requestId = uuidv4();
    this.props.addPendingRequest({
      id: requestId,
      type: AJAX_REQUEST_TYPES.USER_LIBRARY,
      message: 'Fetching Albums'
    });
    try {
      const albums = await axios.post(
        `${API_ROOT}/spotify-search`,
        {
          spotify_path:
            nextAlbums && nextAlbums.length
              ? nextAlbums
              : `https://api.spotify.com/v1/artists/${id}/albums?market=US`,
          is_tracks: false
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );
      this.props.finishPendingRequest(requestId);
      if (!albums || !albums.data) return;
      this.setState({
        albums: [...this.state.albums, ...albums.data.items],
        nextAlbums: albums.data.next
      });
    } catch {
      this.props.finishPendingRequest(requestId);
    }
  }

  render() {
    const {
      searchBy,
      artists,
      nextArtists,
      albums,
      nextAlbums,
      showAlbumTracksTable,
      showTracksTable,
      showArtistAlbums
    } = this.state;

    const { isAuthenticated } = this.props;
    return (
      <div className="search-view-wrapper">
        {!this.state.errorFetchingItems ? (
          <div className="search-view-content">
            <div className="section">
              <div className="container has-text-centered playlists-and-albums-header">
                <p className="title">Spotify Audio Analysis</p>

                {!isAuthenticated ? (
                  <span>
                    If you wish to export playlists,{' '}
                    <a
                      className=""
                      href=""
                      onClick={e => {
                        e.preventDefault();
                        if (this.props.login) {
                          this.props.login();
                        }
                      }}
                    >
                      Login
                    </a>{' '}
                    with Spotify
                  </span>
                ) : null}
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                alignContent: 'center',
                flexDirection: 'column',
                flexWrap: 'wrap'
              }}
            >
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '5px'
                }}
              >
                <button
                  style={{ margin: '2px' }}
                  className={`button is-outlined is-rounded ${
                    searchBy === SearchBy.ARTIST ? 'is-active' : ''
                  }`}
                  onClick={() => {
                    this.setState({
                      searchBy: SearchBy.ARTIST,
                      showAlbumTracksTable: false,
                      showTracksTable: false
                    });
                  }}
                >
                  artists
                </button>
                <button
                  style={{ margin: '2px' }}
                  className={`button is-outlined is-rounded ${
                    searchBy === SearchBy.TRACK ? 'is-active' : ''
                  }`}
                  onClick={() => {
                    this.setState({
                      searchBy: SearchBy.TRACK,
                      showAlbumTracksTable: false,
                      showTracksTable: false
                    });
                  }}
                >
                  tracks
                </button>
              </div>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'center'
                }}
              >
                <form style={{ display: 'inline-flex' }}>
                  <input
                    onChange={e => {
                      if (e.target.value && e.target.value.length) {
                        this.setState({ query: e.target.value });
                        return;
                      }
                      this.setState({ query: '', filtered: false });
                    }}
                    className="input"
                    placeholder={`search for ${searchBy.toLocaleLowerCase()}`}
                  ></input>
                  <button
                    className="button"
                    type="submit"
                    onClick={e => {
                      this.search(e);
                    }}
                  >
                    <SearchIcon />
                  </button>
                </form>
              </div>
            </div>
            {searchBy === SearchBy.ARTIST ? (
              <div style={{ marginTop: '20px' }}>
                {showArtistAlbums ? (
                  <div className="">
                    {searchBy === SearchBy.ARTIST &&
                    artists &&
                    artists.length ? (
                      <div
                        style={{
                          display: 'flex',
                          flexDirection: 'column',
                          alignItems: 'center'
                        }}
                      >
                        <p style={{ margin: 'auto' }}>Select an Artist</p>
                        <div
                          style={{
                            width: '100%',
                            display: 'inline-flex',
                            overflowX: 'scroll',
                            justifyContent: 'flex-start',
                            margin: '10px'
                          }}
                        >
                          {artists.map((artist: any) => (
                            <button
                              key={uuidv4()}
                              className="button"
                              style={{
                                flexShrink: 0,
                                color: 'white',
                                width: 'auto',
                                height: '100px',
                                backgroundImage: `url("${
                                  artist.images[2] ? artist.images[2].url : ''
                                }")`,
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                overflow: 'hidden'
                              }}
                              onClick={e => {
                                e.preventDefault();
                                this.setState({ albums: [] }, () =>
                                  this.getAlbums(artist.id)
                                );
                              }}
                            >
                              <p
                                style={{
                                  color: 'white',
                                  zIndex: 5
                                }}
                              >
                                {artist.name}
                              </p>
                              <div
                                style={{
                                  zIndex: 1,
                                  position: 'absolute',
                                  top: '50%',
                                  left: '50%',
                                  transform: 'translate(-50%,-50%)',
                                  width: '100%',
                                  height: '100%',
                                  background: 'rgba(0,0,0,.6)'
                                }}
                              ></div>
                            </button>
                          ))}
                          {nextArtists ? (
                            <button
                              className="button"
                              onClick={e => {
                                e.preventDefault();
                                this.getArtists();
                              }}
                              style={{
                                width: 'auto',
                                height: '100px',
                                flexShrink: 0
                              }}
                            >
                              Load More
                            </button>
                          ) : null}
                        </div>
                      </div>
                    ) : null}

                    {searchBy === SearchBy.ARTIST && albums && albums.length ? (
                      <div
                        style={{
                          display: 'flex',
                          flexDirection: 'column',
                          alignItems: 'center'
                        }}
                      >
                        <p style={{ margin: 'auto' }}>Select an Album</p>
                        <div
                          style={{
                            width: '100%',
                            display: 'inline-flex',
                            overflowX: 'scroll',
                            justifyContent: 'flex-start',
                            margin: '10px'
                          }}
                        >
                          {albums.map((album: any) => (
                            <button
                              key={uuidv4()}
                              className="button"
                              style={{
                                flexShrink: 0,
                                color: 'white',
                                width: 'auto',
                                height: '100px',
                                backgroundImage: `url("${
                                  album.images &&
                                  album.images[2] &&
                                  album.images[2].url
                                    ? album.images[2].url
                                    : ''
                                }")`,
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                overflow: 'hidden'
                              }}
                              onClick={e => {
                                e.preventDefault();

                                this.setState(
                                  {
                                    showTracksTable: false,
                                    showAlbumTracksTable: false,
                                    showArtistAlbums: false
                                  },
                                  () => {
                                    localStorage.setItem(
                                      'spotify_path',
                                      `https://api.spotify.com/v1/albums/${album.id}/tracks`
                                    );
                                    localStorage.setItem(
                                      'spotify_item_title',
                                      album.name
                                    );
                                    this.setState(
                                      {
                                        showAlbumTracksTable: true,
                                        selectedAlbum: album
                                      },
                                      () => {}
                                    );
                                  }
                                );
                              }}
                            >
                              <p
                                style={{
                                  color: 'white',
                                  zIndex: 5
                                }}
                              >
                                {album.name}
                              </p>
                              <div
                                style={{
                                  zIndex: 1,
                                  position: 'absolute',
                                  top: '50%',
                                  left: '50%',
                                  transform: 'translate(-50%,-50%)',
                                  width: '100%',
                                  height: '100%',
                                  background: 'rgba(0,0,0,.6)'
                                }}
                              ></div>
                            </button>
                          ))}
                          {nextAlbums ? (
                            <button
                              className="button"
                              onClick={e => {
                                e.preventDefault();
                                this.getAlbums('');
                              }}
                              style={{
                                width: 'auto',
                                height: '100px',
                                flexShrink: 0
                              }}
                            >
                              Load More
                            </button>
                          ) : null}
                        </div>
                      </div>
                    ) : null}
                  </div>
                ) : (
                  <div
                    className="card"
                    style={{ display: 'flex', justifyContent: 'center' }}
                  >
                    <button
                      className="button"
                      onClick={() => {
                        this.setState({ showArtistAlbums: true });
                      }}
                    >
                      Expand Search Results
                    </button>
                  </div>
                )}
              </div>
            ) : null}
            {showAlbumTracksTable ? <TracksTable searchResults={true} /> : null}
            {showTracksTable ? <TracksTable searchResults={true} /> : null}
          </div>
        ) : (
          <p className="notification is-danger">
            Error Loading Tracks, Refresh the Page
          </p>
        )}
      </div>
    );
  }
}

function mapStateToProps(state: any) {
  const { isAuthenticated } = state.auth;
  return { isAuthenticated };
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    addFlashMessage: (message: any) =>
      dispatch({ type: ADD_FLASH_MESSAGE, payload: message }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id })
  };
};

export default compose<any>(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(Search);
