import React, { Component } from 'react';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { withRouter } from 'react-router';
import CloseIcon from '@material-ui/icons/Close';
import { GlobalState } from '../../reducers/index.reducer';
import {
  AUTHENTICATE,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST
} from '../../actions/index.actions';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES
} from '../../constants/constants';
import { get_local_storage_status } from '../../checkStorage';

interface AlbumsAndPlaylistsProps {
  route: any;
  history: any;
  type: string;
  addPendingRequest: Function;
  finishPendingRequest: Function;
}

interface AlbumsAndPlaylistsState {
  collection: any[];
  showTracks: boolean;
  tracksPath: string;
  showTable: boolean;
  selectedPlaylistPath: string;
  selectedPlaylistTitle: string;
  errorFetching: boolean;
  query: string;
  spotifyPath: string;
  fetching: boolean;
  collectionType: string;
}

class AlbumsAndPlaylists extends Component<
  AlbumsAndPlaylistsProps,
  AlbumsAndPlaylistsState
> {
  constructor(props: AlbumsAndPlaylistsProps) {
    super(props);
    this.state = {
      collection: [],
      showTracks: false,
      tracksPath: '',
      showTable: false,
      selectedPlaylistPath: '',
      selectedPlaylistTitle: '',
      errorFetching: false,
      query: '',
      spotifyPath:
        this.props.type === 'PLAYLISTS'
          ? 'https://api.spotify.com/v1/me/playlists?limit=50'
          : 'https://api.spotify.com/v1/me/albums?limit=50',
      fetching: false,
      collectionType: this.props.type === 'PLAYLISTS' ? 'Playlist' : 'Album'
    };
    get_local_storage_status();
  }

  componentDidMount() {
    this.getPlaylists();
  }

  async getPlaylists() {
    const requestId = uuidv4();
    try {
      this.setState({ fetching: true });
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.USER_LIBRARY,
        message:
          this.props.type === 'PLAYLISTS'
            ? 'Fetching Playlists'
            : 'Fetching Albums'
      });
      const collection = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: this.state.spotifyPath,
          spotify_payload: null,
          crud_operation: 'get'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      this.props.finishPendingRequest(requestId);

      if (!collection || !collection.data || !collection.data.items) return;

      const { next } = collection.data;

      this.setState(
        {
          collection: [...this.state.collection].concat(
            this.props.type === 'PLAYLISTS'
              ? collection.data.items
              : collection.data.items.map((item: any) => item.album)
          ),
          spotifyPath: next,
          fetching: false
        },
        () => {
          if (!next) return;
          this.getPlaylists();
        }
      );
    } catch (error) {
      this.props.finishPendingRequest(requestId);
      this.setState({ errorFetching: true, fetching: false });
    }
  }

  open(id: string, title: string) {
    localStorage.setItem(
      'spotify_path',
      `https://api.spotify.com/v1/${
        this.props.type === 'PLAYLISTS' ? 'playlists' : 'albums'
      }/${id}/tracks`
    );
    localStorage.setItem('spotify_item_title', title);
    this.props.history.push('/tracks');
  }

  search(query: any) {
    this.setState({ query });
  }

  filterCollection() {
    return [...this.state.collection].filter((item: any) => {
      const { description, name } = item;
      const query = this.state.query.toLocaleLowerCase();
      if (
        (description &&
          description
            .toString()
            .toLowerCase()
            .includes(query)) ||
        (name &&
          name
            .toString()
            .toLowerCase()
            .includes(query))
      )
        return item;
    });
  }

  render() {
    let items =
      this.state.query && this.state.query.length
        ? this.filterCollection()
        : this.state.collection;
    return (
      <div className="playlists-view-wrapper">
        <div className="section">
          <div className="container has-text-centered playlists-and-albums-header">
            <p className="title">Your {this.state.collectionType}s</p>

            <p>Choose {this.state.collectionType} to Select Tracks From</p>
          </div>
        </div>
        <div className="section">
          <div className="container is-widescreen">
            <div className="field">
              <label className="label" htmlFor="search-input">
                Search Your {this.state.collectionType}s
              </label>
              <div className="search-playlists-input-wrapper">
                <input
                  id="search-input"
                  className="search-input input"
                  onChange={e => this.search(e.target.value)}
                  placeholder="Search"
                  value={this.state.query}
                ></input>
                {this.state.query.length ? (
                  <button
                    onClick={() => {
                      this.setState({ query: '' });
                    }}
                  >
                    <CloseIcon className="has-text-link" />
                  </button>
                ) : null}
              </div>
            </div>
            {!this.state.errorFetching ? (
              <div className="playlists-wrapper">
                {items.length ? (
                  items.map((item: any) => (
                    <div
                      style={{
                        backgroundImage:
                          item.images && item.images[0] && item.images[0].url
                            ? `url(${item.images[0].url})`
                            : 'none',
                        backgroundSize: 'cover',
                        backgroundPosition: 'center'
                      }}
                      key={uuidv4()}
                      className="playlist-wrapper has-background-grey-lighter"
                      onClick={() => this.open(item.id, item.name)}
                    >
                      <p
                        style={{
                          color: 'white',
                          position: 'absolute',
                          top: '50%',
                          left: '50%',
                          transform: 'translate(-50%,-50%)',
                          zIndex: 5,
                          padding: '10px',
                          background: 'rgba(0,0,0,.8)',
                          borderRadius: '5px'
                        }}
                      >
                        {item.name}
                      </p>
                      <div
                        style={{
                          width: '100%',
                          height: '100%',
                          position: 'absolute',
                          top: '50%',
                          left: '50%',
                          transform: 'translate(-50%,-50%)',
                          background: 'rgba(0,0,0,.4)',
                          zIndex: 2
                        }}
                      ></div>
                    </div>
                  ))
                ) : !this.state.fetching ? (
                  <p>no results</p>
                ) : null}
              </div>
            ) : (
              <p className="notification is-danger">
                Error Loading {this.state.collectionType}s, Refresh the Page
              </p>
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    authenticate: () => dispatch({ type: AUTHENTICATE }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id })
  };
};

function mapStateToProps(state: GlobalState) {
  return {};
}

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(AlbumsAndPlaylists);
